<template>
  <div class="helloWorld">
    
    <div>
      <button
        class="btn-video" :style="{ 
          backgroundColor: bShowScanner ? '#75c3ff' : '#343a40',
          Color: bShowScanner ? '#212529' : '#dbdbdb',
          fontWeight: bShowScanner ? 'bold' : 'normal',
          textDecoration: bShowScanner ? 'underline' : ''
          }" 
                    @click="showScanner">Камера</button> 
      <button 
        class="btn-img" :style="{ 
          backgroundColor: bShowImgDecode ? '#75c3ff' : '#343a40',
          Color: bShowScanner ? '#212529' : '#dbdbdb',
          fontWeight: bShowScanner ? 'normal' : 'bold',
          textDecoration: bShowScanner ? '' : 'underline'
          }" @click="showImgDecode">Фото</button>
    </div>
    <div class="container">
      <VideoDecode v-if="bShowScanner"></VideoDecode>
      <ImgDecode v-if="bShowImgDecode"></ImgDecode>
    </div>
    <div class="imei_result" v-if="bars_array.length > 0">
      <table class="w3-table w3-striped">
         <tr v-for="(b,index) in bars_array" :key="index">
              <td class="imei_out">
               {{ b}}
              </td>
         </tr>
      </table>
    </div>
  </div>
</template>

<script>
import "../dbr"; // import side effects. The license, engineResourcePath, so on.
import { BarcodeReader } from "dynamsoft-javascript-barcode";
import VideoDecode from "./VideoDecode.vue";
import ImgDecode from './ImgDecode.vue'
import {store} from '../store'
export default {
  name: "HelloWorld",
  data() {
    return {
      bShowScanner: true,
      bShowImgDecode: false,
      
    };
  },
  computed:{
    bars_array(){
      return store.state.bars_array
    
    }
  }
   ,
  async mounted() {
    //Load the library on page load to speed things up.
    try {
      await BarcodeReader.loadWasm();
    } catch (ex) {
      let errMsg;
      if (ex.message?.includes("network connection error")) {
        errMsg = "Failed to connect to Dynamsoft License Server: network connection error. Check your Internet connection or contact Dynamsoft Support (support@dynamsoft.com) to acquire an offline license.";
      } else {
        errMsg = ex.message||ex;
      }
      console.error(errMsg);
      alert(errMsg);
    }
  },
  components: {
    VideoDecode,
     ImgDecode
  },
  methods: {
    showScanner() {
      this.bShowScanner = true;
      this.bShowImgDecode = false;
    },
    showImgDecode() {
      this.bShowScanner = false;
      this.bShowImgDecode = true;
    }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  font-size: 1.5em;
}

.btn-video{
  border-radius: 5px 0 0 5px;
  width: 300px;
}
.btn-img{
  border-radius: 0 5px 5px 0;
  width: 300px;
}


.applogo {
  height: 25px;
}

.helloWorld {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  color: #455a64;
}

button {
  font-size: 1.5rem;
  margin-bottom: 2vh;
  border: none;
}

.container {
  margin: 2vmin auto;
  text-align: center;
  font-size: medium;
  width: 100%;
}

.imei_result{
  margin: 2vmin auto;
  text-align: center;
  font-size: medium;
  width: 600px;
  border: 2px rgb(117, 195, 255) solid;
  border-radius: 10px;
}
.imei_out{
  font-size: 20px;
  text-align: center;
}
</style>
